import { Component, OnInit, Injectable, ChangeDetectorRef, ViewChild } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { Validators, FormBuilder, FormGroup, FormControl, ValidatorFn, AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { RfidService } from 'app/shared/services/rfid/rfid.service';
import { DatePipe } from '@angular/common';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { ClientService } from 'app/shared/services/client/client.service';
import { BehaviorSubject, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MenuConfigService } from 'app/shared/services/menu-config/menu-config.service';
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { RoleService } from 'app/shared/services/user/role.service';
import { UtilityService } from 'app/shared/services/utility/utility.service';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
export class TodoItemNodeTest {
  children: TodoItemNodeTest[];
  item: string;
  actionNode: ActionNode[];
  id: number;
  title: string;
  menu_id: string;
  isSelected: boolean = false;
  parent_id: string;
  rm_map_id: string;
}
export class ActionNode {
  add: boolean;
  delete: boolean;
  download: boolean;
}

/** Flat to-do item node with expandable and level information */
export class TodoItemFlatNode {
  item: string;
  level: number;
  expandable: boolean;
  actionAll: ActionMenuNode[];
  children: TodoItemFlatNode[];
  actionNode: ActionNode[];
  id: number;
  title: string;
  menu_id: string;
  map_id: string;
  parent_id: string;
  rm_map_id: string;
  isSelected: boolean = false;
}
export class ActionMenuNode {
  add: boolean;
  delete: boolean;
  download: boolean;
  selectAll: boolean;
}
@Injectable()
export class ChecklistDatabase {
  dataChange = new BehaviorSubject<TodoItemFlatNode[]>([]);
  localStorage: any;
  get data(): TodoItemFlatNode[] { return this.dataChange.value; }

  constructor(private _menuConfigService: MenuConfigService, private changeDetectorRef: ChangeDetectorRef) {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.initialize();
  }

  initialize() {
    this.getRoleMenuList(this.localStorage.project_id, this.localStorage.client_id, this.localStorage.role_id);


  }
  getRoleMenuList(project_id, client_id, role_id) {
    this._menuConfigService.getMenusByClientIdWithAlreadyMappedToRole(project_id, client_id, role_id).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          let menuConfigLit = [];
          let menuChildList = [];
          for (let index = 0; index < resp.data.length; index++) {
            let menuCfg = resp.data[index];
            if (menuCfg.parent_id == null) {
              menuConfigLit.push(menuCfg);
            } else {
              let parentMenu = this.getParentMenu(menuConfigLit, menuCfg.parent_id);
              menuChildList = parentMenu.children;
              if (undefined == menuChildList) {
                menuChildList = [];
                parentMenu.children = menuChildList;
              }
              menuChildList.push(menuCfg);
            }
          }
          this.changeDetectorRef.detectChanges();
          const data = this.buildFileTree(menuConfigLit, 0);
          this.dataChange.next(data);


        } else {

        }

      }, (err) => {
      }
    )
  }
  getParentMenu(menuConfigList, parent_id) {
    let parentMenu;
    for (let index = 0; index < menuConfigList.length; index++) {
      let menuConfig = menuConfigList[index];
      if (menuConfig.menu_id == parent_id) {
        parentMenu = menuConfig;
        break;
      } else {
        let menuChildList = menuConfig.children;
        if (undefined != menuChildList) {
          parentMenu = this.getParentMenu(menuChildList, parent_id);
          if (parentMenu != undefined) {
            break;
          }
        }
      }
    }
    return parentMenu;
  }
  buildFileTree(menudata, level: number): TodoItemFlatNode[] {
    let todoItemList = [];
    for (let index = 0; index < menudata.length; index++) {
      let menuCfg = menudata[index];
      const node = new TodoItemFlatNode();
      node.item = menuCfg.title;
      node.id = menuCfg.id;
      node.title = menuCfg.title;
      node.menu_id = menuCfg.menu_id;
      node.map_id = menuCfg.map_id;
      node.parent_id = menuCfg.parent_id;
      node.rm_map_id = menuCfg.rm_map_id;
      this.changeDetectorRef.detectChanges();
      if (menuCfg.children != undefined)
        node.children = this.buildFileTree(menuCfg.children, level + 1);
      todoItemList.push(node);

    }
    return todoItemList;
  }

  insertItem(parent: TodoItemFlatNode, name: string) {
    if (parent.children) {
      parent.children.push({ item: name } as TodoItemFlatNode);
      this.dataChange.next(this.data);
    }
  }

  updateItem(node: TodoItemFlatNode, name: string) {
    node.item = name;
    this.dataChange.next(this.data);
  }
}

export interface MenuAction {
  name: string;
  completed: boolean;
  subMenu?: MenuAction[];
}
@Component({
  selector: 'app-role-menu',
  templateUrl: './role-menu.component.html',
  styleUrls: ['./role-menu.component.scss'],
  providers: [ChecklistDatabase, DatePipe],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
})
export class RoleMenuComponent implements OnInit {
  clients: any;
  filteredOptionsClient: Observable<any[]>;
  roles: any;
  filteredOptionsRole: Observable<any[]>;
  roleMenuForm: FormGroup;
  actionMenu = false;
  menuNode = false;
  localStorage: any;
  flatNodeMap = new Map<TodoItemFlatNode, TodoItemFlatNode>();
  menuAction: MenuAction = {
    name: 'select All',
    completed: false,
    subMenu: [
      { name: 'Add/Update', completed: false },
      { name: 'Delete', completed: false },
      { name: 'Download', completed: false }
    ]
  };
  selectedClientID: any;
  /** Map from nested node to flattened node. This helps us to keep the same object for selection */
  nestedNodeMap = new Map<TodoItemFlatNode, TodoItemFlatNode>();
  TREE_DATA: any;
  /** A selected parent node to be inserted */
  selectedParent: TodoItemFlatNode | null = null;

  /** The new item's name */
  newItemName = '';

  treeControl: FlatTreeControl<TodoItemFlatNode>;

  treeFlattener: MatTreeFlattener<TodoItemFlatNode, TodoItemFlatNode>;

  dataSource: MatTreeFlatDataSource<TodoItemFlatNode, TodoItemFlatNode>;

  /** The selection for checklist */
  checklistSelection = new SelectionModel<TodoItemFlatNode>(true /* multiple */);
  @ViewChild('tree') tree;
  @ViewChild("toggleElement") ref: any;
  @ViewChild("toggleCollasped") collaspedRef: any;
  selectChange = false;
  collaspedChange = false;
  selectedMenus = [];
  menuArray: any;
  constructor(private snackBar: MatSnackBar, private menuConfigService: MenuConfigService, private changeDetectorRef: ChangeDetectorRef, private fb: FormBuilder, private clientService: ClientService, private _database: ChecklistDatabase,
    private roleService: RoleService,private _utilityService:UtilityService) {
    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel,
      this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<TodoItemFlatNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    this.getMenuListClientWise();


  }

  ngOnInit() {

    this.initialize();
  }

  ngAfterViewInit() {
    this._utilityService.activeMenuItems$.subscribe(menuItemSelecte=> {
      this.menuArray = menuItemSelecte;
      if(this.menuArray == null || this.menuArray == undefined || this.menuArray.title == null || this.menuArray.title == undefined){
        this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
      }
      this.changeDetectorRef.detectChanges();
    })
    this.treeControl.expandAll();
  }
  
  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.roleMenuForm = this.fb.group({
      client: ['', [autocompleteObjectValidator(), Validators.required]],
      role: ['', [autocompleteObjectValidator(), Validators.required]],
    });
    this.getClients();
    this.getRolesList(this.localStorage.client_id);
  }
  getMenuListClientWise() {
    this._database.dataChange.subscribe(data => {
      this.dataSource.data = data;
      this.treeControl.expandAll()

    });
  }


  nodeSelectionToggle(node: TodoItemFlatNode): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants, node)
      : this.checklistSelection.deselect(...descendants, node);
    this.changeDetectorRef.markForCheck();
  }

  checkParentIsSelected(data) {
    for (var k = 0; k < data.length; k++) {
      let node = data[k];
      if (node.children != undefined) {
        const children = node.children;
        this.checkParentIsSelected(children);
        let result = false;

        for (var i = 0; i < children.length; i++) {
          const isSelected = this.selectedMenus.filter(t => { return children[i].menu_id == t.menu_id });
          if (isSelected != null && isSelected.length > 0) {
            result = true;
            break;
          }
        }

        if (result == true) {
          const isAlreadyPush = this.selectedMenus.filter(t => { return node.menu_id == t.menu_id });
          if (isAlreadyPush == null || isAlreadyPush.length == 0) {
            this.selectedMenus.push(node);
          }
        }

      }

    }
  }

  openSubmit() {
    let formValues = this.roleMenuForm.value;
    ////
    this.selectedMenus = [];
    this.selectedMenus = [...this.checklistSelection.selected];

    this.checkParentIsSelected(this.dataSource.data);

    //;
    let params = {
      role_id: formValues.role.id,
      menus: this.selectedMenus.map(v => ({ menu_id: v.menu_id })),
      status: "Y",
      created_by: this.localStorage.userId
    }


    this.menuConfigService.setRoleMenuMapping(params).subscribe(
      (resp: any) => {
        // 
        if (resp.status = true) {
          this.snackBar.open(MessageConstant.roleMenuMappedSucess, 'Dismiss', { duration: 5000 });
          this._database.getRoleMenuList(this.localStorage.project_id, formValues.client.id, formValues.role.id)
        } else {
          this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
        }



      },
      (err) => {
        // 
        this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
      }
    )





  }
  getClients() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (data: any) => {
        this.clients = data;

        const toSelect = this.clients.find(c => c.id == this.localStorage.client_id);
        this.roleMenuForm.get('client').setValue(toSelect);
        this.selectedClientID = toSelect.id;
        this.filteredOptionsClient = this.roleMenuForm.controls.client.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterClientModels(name) : this.clients)
          );

      }, (err) => {

      }
    )
  }
  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }

  private _filterClientModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  onClientChange(value) {
    //this.dataSource.data =[];
    //this._database.dataChange.next(null);
    //this.checklistSelection.clear();
    //this.descendantsAllSelected( this.checklistSelection.);
    //this._database.getRoleMenuList(this.localStorage.project_id,value.id);
    this.getRolesList(value.id);
    this.roleMenuForm.get('role').setValue('');
    this.selectedClientID = value.id;
    //this.getMenuListClientWise();
  }

  getRolesList(client_id) {
    this.roles = [];
    this.roleService.getActiveRolesByClientId(this.localStorage.project_id, client_id).subscribe(
      (resp: any) => {
        if (resp.status = true) {
          this.roles = resp.data;

          const toSelect = this.roles.find(c => c.id == this.localStorage.role_id);
          if (toSelect != null) {
            if (toSelect.id = this.localStorage.role_id) {
              this.roleMenuForm.get('role').setValue(toSelect);
            }

          }


          this.filteredOptionsRole = this.roleMenuForm.controls.role.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterRoleModels(name) : this.roles)
            );
          //this._database.getRoleMenuList(this.localStorage.project_id,client_id,toSelect.id); 
        }


      }, (err) => {

      }
    )
  }
  displayRole(item: any): string {
    return item && item.name ? item.name : '';
  }

  private _filterRoleModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.roles.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  onRoleChange(value) {

    this.checklistSelection.clear();
    this._database.getRoleMenuList(this.localStorage.project_id, this.selectedClientID, value.id);
  }
  getLevel = (node: TodoItemFlatNode) => node.level;

  isExpandable = (node: TodoItemFlatNode) => node.expandable;
  menu_id = (node: TodoItemFlatNode) => node.menu_id;
  parent_id = (node: TodoItemFlatNode) => node.parent_id;
  getChildren = (node: TodoItemFlatNode): TodoItemFlatNode[] => node.children;

  hasChild = (_: number, _nodeData: TodoItemFlatNode) => _nodeData.expandable;

  hasNoContent = (_: number, _nodeData: TodoItemFlatNode) => _nodeData.item === '';

  /**
   * Transformer to convert nested node to flat node. Record the nodes in maps for later use.
   */
  transformer = (node: TodoItemFlatNode, level: number) => {
    const existingNode = this.nestedNodeMap.get(node);
    const flatNode = existingNode && existingNode.item === node.item
      ? existingNode
      : new TodoItemFlatNode();
    flatNode.item = node.item;
    flatNode.level = level;
    flatNode.menu_id = node.menu_id;
    flatNode.parent_id = node.parent_id;
    flatNode.expandable = !!node.children?.length;
    this.flatNodeMap.set(flatNode, node);
    this.nestedNodeMap.set(node, flatNode);
    if (node.rm_map_id != null) {
      this.nodeSelectionToggle(flatNode);
    }
    return flatNode;
  }

  /** Whether all the descendants of the node are selected. */
  // descendantsAllSelected(node: TodoItemFlatNode): boolean {
  //   const descendants = this.treeControl.getDescendants(node);

  //   if (!descendants.length) {
  //     return this.checklistSelection.isSelected(node);
  //   }
  //   const selected = this.checklistSelection.isSelected(node);
  //   const allSelected = descendants.every(child => this.checklistSelection.isSelected(child));
  //   if (!selected && allSelected) {
  //     this.checklistSelection.select(node);

  //   }
  //   return allSelected;
  // }


  descendantsAllSelected(node: TodoItemFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.length > 0 && descendants.every(child => {
      this.changeDetectorRef.markForCheck();
      return this.checklistSelection.isSelected(child);
    });
    return descAllSelected;
  }

  /** Whether part of the descendants are selected */
  descendantsPartiallySelected(node: TodoItemFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  /** Toggle the to-do item selection. Select/deselect all the descendants node */
  todoItemSelectionToggle(node: TodoItemFlatNode): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants.forEach(child => this.checklistSelection.isSelected(child));
    this.checkAllParentsSelection(node);
    this.actionMenu = true;
    this.menuNode = true;
  }

  /** Toggle a leaf to-do item selection. Check all the parents to see if they changed */
  todoLeafItemSelectionToggle(node: TodoItemFlatNode): void {
    this.checklistSelection.toggle(node);
    this.checkAllParentsSelection(node);

  }

  /* Checks all the parents when a leaf node is selected/unselected */
  checkAllParentsSelection(node: TodoItemFlatNode): void {
    let parent: TodoItemFlatNode | null = this.getParentNode(node);
    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
    this.actionMenu = true;
  }

  /** Check root node checked state and change it accordingly */
  checkRootNodeSelection(node: TodoItemFlatNode): void {
    const nodeSelected = this.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.length > 0 && descendants.every(child => {
      return this.checklistSelection.isSelected(child);
    });
    if (nodeSelected && !descAllSelected) {
      this.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.checklistSelection.select(node);
    }
  }

  /* Get the parent node of a node */
  getParentNode(node: TodoItemFlatNode): TodoItemFlatNode | null {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }

  /** Select the category so we can insert the new item. */
  addNewItem(node: TodoItemFlatNode) {
    const parentNode = this.flatNodeMap.get(node);
    this._database.insertItem(parentNode!, '');
    this.treeControl.expand(node);
  }

  /** Save the node to database */
  saveNode(node: TodoItemFlatNode, itemValue: string) {
    const nestedNode = this.flatNodeMap.get(node);
    this._database.updateItem(nestedNode!, itemValue);
  }
  allComplete: boolean = false;

  updateAllComplete() {
    this.allComplete = this.menuAction.subMenu != null && this.menuAction.subMenu.every(t => t.completed);
  }

  someComplete(node: any): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
    // if (node.this.menuAction.subMenu == null) {
    // return false;
    // }
    //return node.this.menuAction.subMenu.filter(t => t.completed).length > 0 && !this.allComplete;
  }

  setAll(completed: boolean) {
    this.allComplete = completed;
    if (this.menuAction.subMenu == null) {
      return;
    }
    this.menuAction.subMenu.forEach(t => t.completed = completed);
  }
  updateSelectChange(event) {
    this.selectChange = event.checked;
    this.ref.checked = this.selectChange;
    if (this.selectChange == true) {
      this.checklistSelection.clear();
      this.selectAll();

      // this.treeControl.collapseAll();
    } else {
      this.unSelectAll();
      this.checklistSelection.clear();
      //this.checklistSelection.selected
      //this.treeControl.expandAll();
    }
  }
  updatecollaspedChange(event) {
    this.collaspedChange = event.checked;
    this.collaspedRef.checked = this.collaspedChange;
    if (this.collaspedChange == true) {
      this.treeControl.collapseAll();
    } else {
      this.treeControl.expandAll();
    }
  }
  selectAll() {
    for (let i = 0; i < this.treeControl.dataNodes.length; i++) {
      if (!this.checklistSelection.isSelected(this.treeControl.dataNodes[i]))
        this.checklistSelection.toggle(this.treeControl.dataNodes[i]);
      this.treeControl.expand(this.treeControl.dataNodes[i])
    }
  }
  unSelectAll() {
    for (let i = 0; i < this.treeControl.dataNodes.length; i++) {
      if (!this.checklistSelection.isSelected(this.treeControl.dataNodes[i]))
        this.checklistSelection.toggle(this.treeControl.dataNodes[i]);
      this.treeControl.expand(this.treeControl.dataNodes[i])
    }
  }
}



