import { Component, OnInit, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
// import { ChargerService } from '@shared/services/client/client.service';
import { RoleService } from 'app/shared/services/user/role.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ClientService } from 'app/shared/services/client/client.service';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class CreateComponent implements OnInit {


  form: FormGroup;
  filteredOptionsClient: Observable<any[]>;
  clients: any;
  localStorage: any;
  messageConstant: MessageConstant;
  @ViewChild("toggleElement") ref: any;
  status = true;
  constructor(private fb: FormBuilder,
    private roleService: RoleService, private snackBar: MatSnackBar,
    private router: Router, private clientService: ClientService,) {


  }

  ngOnInit() {
    this.initialize();
    this.getClients();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      client: ['', [autocompleteObjectValidator(), Validators.required]],
      code: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      description: ['', [Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      status: [{ value: false }, [Validators.required]]
    });

  }

  onSubmit() {
    if (this.form.valid) {
      let formValues = this.form.value;

      let params = {
        client_id: formValues.client.id,
        code: formValues.code,
        name: formValues.name,
        project_id: this.localStorage.project_id,
        description: formValues.description,
        status: this.status == true ? 'Y' : 'N',
        created_by: this.localStorage.userId
      }

      this.roleService.create(params).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.snackBar.open(MessageConstant.roleCreated, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/userManagement/role/crud/list']);
          } else if (resp.status == false) {
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          } else {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        },
        (err) => {
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )
    } else {
      return;
    }


  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }
  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  getClients() {


    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (data: any) => {
        this.clients = data;

        const toSelect = this.clients.find(c => c.id == this.localStorage.client_id);
        this.form.get('client').setValue(toSelect);
        this.filteredOptionsClient = this.form.controls.client.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterClientModels(name) : this.clients)
          );
      }, (err) => {

      }
    )

  }
  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterClientModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }
}


